import React from 'react';
import all from './allFlag.jpg';
import './Header.css'
const Header = () => {
    const headerStyle = {
        backgroundImage: `url(${all})`,
        padding: '5%',
        
    }
    return (
        <div style={headerStyle}>

            <h1 className="text-center countryHead" >Find things about your country here!!!  </h1>
        </div>
    );
};

export default Header;