import React, { useEffect, useState } from 'react';
import { Button, Container, Card, Row, Col } from 'react-bootstrap';
import { useParams } from 'react-router';
import { Link } from 'react-router-dom';
import './CountryDetails.css'

const CountryDetails = ({country}) => {
const {id} = useParams()
const [details, setDetails] = useState('')
useEffect(()=>{
    const url = `https://restcountries.eu/rest/v2/alpha/${id}`
    fetch(url)
    .then(res => res.json())
    .then(data => setDetails(data))

},[id])
console.log(details.name);
    return (
        <div >
           
            <Container id='details' className='mt-5 '>
                <Row>
                    <Col>
                    <h1>Country Name: {details?.name} </h1>
                    <h1>Capital: {details?.capital}</h1>
                    <h1>Region: {details?.region}</h1>
                    <h1>Population: {details?.population}</h1>
                    <h1>Domain: "{details?.topLevelDomain}"</h1>
                    <h1>Alpha Code 3: "{details?.alpha3Code}"</h1>
                
                     </Col>
                    <Col>  <img className='mt-5 image' width='350px' style={{border: '2px solid white', padding:'2%',borderRadius: '5px'}} src={details?.flag}  alt="That should be a flag"/> </Col>
                </Row>
                <Link   to='/'>
                    <Button className='mt-5' variant="outline-light" >back to home</Button>
                </Link>
            </Container>

        </div>
    );
};

export default CountryDetails;